<template>
  <div>
    <HeaderTop/>
    <Header/>
    <div class="sentWrap">
      <div class="componentSection">
        <div>
          <SidemenuLogin/>
          <SidemenuSent action="1"/>
          <SidemenuHelp/>
        </div>
      <div class="sentContainer">
        <div class="sentHeader">
          <h2>예약내역</h2>
        </div>
        <div class="sentNav">
          <button :class="{'active': menuIndex === 0 }" @click="menuChange(0)">단문 예약내역</button>
          <button :class="{'active': menuIndex === 1 }" @click="menuChange(1)">장문 예약내역</button>
          <button :class="{'active': menuIndex === 2 }" @click="menuChange(2)">사진 예약내역</button>
        </div>
        <div v-if="menuIndex === 0">
          <ReserSms/>
        </div>
        <div v-else-if="menuIndex === 1">
          <ReserLms/>
        </div>
        <div v-else-if="menuIndex === 2">
          <ReserMms/>
        </div>
      </div>
      </div>
    </div>
    <Footer/>
  </div>
</template>

<script>
import Header from '../Header'
import HeaderTop from '../HeaderTop'
import Footer from '../Footer'
import ReserSms from '../ReserSms'
import ReserLms from '../ReserLms'
import ReserMms from '../ReserMms'
import SidemenuLogin from '../SidemenuLogin'
import SidemenuSent from '../SidemenuSent'
import SidemenuHelp from '../SidemenuHelp'

export default {
  name: 'Reservatione',
  components: {
    Header,
    Footer,
    HeaderTop,
    ReserSms,
    ReserLms,
    ReserMms,
    SidemenuLogin,
    SidemenuSent,
    SidemenuHelp
  },
  data () {
    return {
      menuIndex: 0
    }
  },
  methods: {
    menuChange (index) {
      this.menuIndex = index
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
    outline: none;
    list-style: none;
}
.sentWrap {
    width: 100%;
}
.componentSection {
    width: 1100px;
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
}
.sentContainer {
    width: 820px;
}
.sentHeader {
  border-bottom: 2px solid #ccc;
  box-sizing: border-box;
  padding-bottom: 7px;
  margin-top: 10px;
}
.sentNav button{
    width: 200px;
    height: 50px;
    border: 0;
    margin-top: 10px;
    border-radius: 10px;
}
</style>



