<template>
  <div>
    <HeaderTop/>
    <Header/>
    <div class="csWrap">
        <div class="componentSection">
          <div>
            <SidemenuLogin/>
            <SidemenuListCustomer action="2"/>
            <SidemenuHelp/>
          </div>
            <div class="csContainer">
                <div class="csHeader">
                    <h2>1:1 문의</h2>
                </div>
                <div class="csInfo">
                    <p>*문의사항 및 요청사항을 남겨주시면 답변드리겠습니다.</p>
                    <p>*주소록 대행입력 및 발송대행 서비스에 대한 요청도 가능합니다.</p>
                </div>
                <div class="csList">
                    <table>
                        <tr>
                            <th>번호</th>
                            <th>제목</th>
                            <th>작성일자</th>
                        </tr>
                        <tr v-for="(q, index) in list" :key="index">
                            <td>{{ q.number }}</td>
                            <td><a href="/#/InquiriesTexting">{{ q.title }}</a></td>
                            <td>{{ q.day }}</td>
                        </tr>
                    </table>
                    <div class="csListButton"><button type="button" onclick="location.href='/#/InquiriesWriting'">문의하기</button></div>
                </div>
            </div>
        </div>
    </div>
    <Footer/>
  </div>
</template>

<script>
import Header from '../Header'
import HeaderTop from '../HeaderTop'
import Footer from '../Footer'
import SidemenuLogin from '../SidemenuLogin'
import SidemenuListCustomer from '../SidemenuListCustomer'
import SidemenuHelp from '../SidemenuHelp'

export default {
  name: 'Inquiries',
  components: {
    Header,
    Footer,
    HeaderTop,
    SidemenuLogin,
    SidemenuListCustomer,
    SidemenuHelp
  },
  data () {
    return {
      list: [
        {
          number: '문의',
          title: '고객센터문의',
          day: '2016-05-12'
        }
      ]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
    outline: none;
    list-style: none;
}
.csWrap {
    width: 100%;
}
.csContainer {
    width: 820px;
}
.csHeader {
    border-bottom: 2px solid #ccc;
    box-sizing: border-box;
    padding-bottom: 7px;
    margin-top: 10px;
}
.csInfo {
    width: 100%;
    border: 1px solid #ccc;
    box-sizing: border-box;
    padding: 10px 20px;
    margin: 10px 0px 20px;
}
.csList {
    text-align: center;
    line-height: 2;
}
.csList > table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 10px;
}
.csList > table > tr > th {
    border: 1px solid #ccc;
}
.csList > table > tr > td {
    border: 1px solid #ccc;
}
.csList > table > tr > th:nth-child(1) {
    width: 80px;
}
.csList > table > tr > th:nth-child(3) {
    width: 120px;
}
.csListButton {
    width: 100%;
    text-align: right;
}
.csListButton > button {
    width: 100px;
    height: 30px;
    border: 0;
    background: rosybrown;
    margin-right: 10px;
}
</style>



