<template>
<div>
    <div class="chargingContent">
        <table>
            <tr>
                <th>선택</th>
                <th>결제금액</th>
                <th>단문(SMS)</th>
                <th>장문(LMS)</th>
                <th>포토(MMS)</th>
            </tr>
            <tr v-for="(d, index) in listData" :key="index">
                <td><input type="radio"></td>
                <td>{{ d.pay }}원</td>
                <td>{{ d.sms }}건</td>
                <td>{{ d.lms }}건</td>
                <td>{{ d.mms }}건</td>
            </tr>
        </table>
    </div>
</div>
</template>

<script>
export default {
  name: 'CreditCard',
  data () {
    return {
      listData: [
        {
          pay: '10,000',
          sms: '697',
          lms: '259',
          mms: '70'
        },
        {
          pay: '20,000',
          sms: '1400',
          lms: '259',
          mms: '70'
        },
        {
          pay: '30,000',
          sms: '2100',
          lms: '259',
          mms: '70'
        },
        {
          pay: '40,000',
          sms: '2800',
          lms: '259',
          mms: '70'
        },
        {
          pay: '50,000',
          sms: '3500',
          lms: '259',
          mms: '70'
        }
      ]
    }
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.chargingContent > table {
  width: 100%;
  text-align: center;
  line-height: 2;
  border: 1px solid #ccc;
  border-collapse: collapse;
  margin: 20px 0;
}
.chargingContent > table > tr > th {
  border: 1px solid #ccc;
  color:orchid;
}
.chargingContent > table > tr > td {
  border: 1px solid #ccc;
}
</style>



