          <div v-if="menuIndex === 0">
            <Sms/>
          </div>
          <div v-else-if="menuIndex === 1">
            <Lms/>
          </div>
          <div v-else-if="menuIndex === 2">
            <Mms/>
          </div>
          <div v-else-if="menuIndex === 3">
          </div>
            <div class="shipmentContainer__top--left--phoneButton">
              <button :class="{'active': menuIndex === 0 }" @click="menuChange(0)">SMS단문</button>
              <button :class="{'active': menuIndex === 1 }" @click="menuChange(1)">LMS장문</button>
              <button :class="{'active': menuIndex === 2 }" @click="menuChange(2)">MMS사진</button>
              <button :class="{'active': menuIndex === 3 }" @click="menuChange(3)"></button>
            </div>
          </div>
          <div class="shipmentContainer__top--right">
            <div class="shipmentContainer__top--right--recipient">
              <div>수신번호 입력<input type="text"><button>추가하기</button></div>
              <div>번호 검색<input type="text"><button>검색</button></div>
              <div class="addressList">
              <table class="addressListTable">
                <tr>
                  <th>그룹명</th>
                  <th>이름</th>
                  <th>번호</th>
                </tr>
                <tr v-for="(a, index) in list" :key="index">
                  <td>{{ a.group }}</td>
                  <td>{{ a.name }}</td>
                  <td>{{ a.number }}</td>
                  <td><button>삭제</button></td>
                </tr>
              </table>
              </div>
            </div>
          </div>
        </div>
        <div class="shipmentContainer__bottom">
          <div class="shipmentContainer__bottom__group">
            <select v-model="group">
              <option disabled value="2">그룹명</option>
              <option value="0">개발팀</option>
              <option value="1">기획팀</option>
            </select>
          </div>
