                    <th class="th__right">일반 전화번호</th>
                    <th>이메일</th>
                    <th><input type="checkbox"></th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(aItem, i) in option[selectIndex].value" :key="i">
                  <td>{{aItem.a}}</td>
                  <td>{{aItem.b}}</td>
                  <td>{{aItem.c}}</td>
                  <td>{{aItem.d}}</td>
                  <td>{{aItem.e}}</td>
                  <td><input type="checkbox"></td>
