<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
    outline: none;
    list-style: none;
}
.addressList {
  width: 100%;
  max-height: 400px;
  text-align: center;
  overflow-x: hidden;
  margin-top: 51px;
}
.addressListTable {
  border-collapse: collapse;
  width: 100%;
}
.addressListTable > tr {
  height: 30px;
}
.addressListTable > tr > th {
  border: 1px solid #000;
}
.addressListTable > tr > td {
  border: 1px solid #000;
}
.shipment {
  width: 100%;
}
.shipmentContainer {
  width: 100%;
  max-width: 820px;
  min-width: 750px;
}
.shipmentContainerHeader {
  border-bottom: 2px solid #ccc;
  box-sizing: border-box;
  padding-bottom: 7px;
  margin-top: 10px;
}
.shipmentContainer__top {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}
.shipmentContainer__top--left {
  display: flex;
  justify-content: center;
}
.shipmentContainer__top--left--phone {
