  right: 1px;
  background: #e3f6fe;
}

.phoneTitle {
  padding-left: 15px;
  font-size: 20px;
  border-bottom: 1px solid #ccc;
}
.phoneTitle > input {
  width: 375px;
  height: 25px;
  border: 0;
  font-size: 17px
}
.text {
  border: 0;
  padding: 15px;
  background-color: #e3f6fe;
}
.shipmentContainer__top--right {
  display: flex;
  justify-content: center;
}
.shipmentContainer__top--right--recipient {
  max-width: 400px;
  min-width: 350px;
  height: 500px;
  border: 1px solid #ccc;
}
.shipmentContainer__bottom {
  width: 100%;
  height: auto;
}
.shipmentContainer__bottom__group {
  margin-top: 20px;
}
.shipmentContainer__bottom__group > select {
  width: 100px;
  height: 35px;
  border-radius: 5px;
  padding: 0px 10px;
}
.shipmentContainer__bottom__table {
    width: 100%;
    height: 100%;
    border-collapse: collapse;
    margin-top: 5px;
}
.shipmentContainer__bottom__table thead {
    width: 100%;
    height: 40px;
    background: #c9c9c9;
    border-top: 2px solid #000;
}
.th__right {
    border-right: 1px solid #000;
}
</style>



