import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/page/Index'
import Shipment from '@/components/page/Shipment'
import SentList from '@/components/page/SentList'
import Login from '@/components/page/Login'
import Address from '@/components/page/Address'
import CustomerService from '@/components/page/CustomerService'
import Charging from '@/components/page/Charging'
import AddressSpam from '@/components/page/AddressSpam'
import Reservatione from '@/components/page/Reservatione'
import Inquiries from '@/components/page/Inquiries'
import Faq from '@/components/page/Faq'
import Terms from '@/components/page/Terms'
import FindId from '@/components/page/FindId'
import FindPass from '@/components/page/FindPass'
import PatmentDetails from '@/components/page/PatmentDetails'
import UserInfo from '@/components/page/UserInfo'
import Spam from '@/components/page/Spam'
import Personal from '@/components/page/Personal'
import Email from '@/components/page/Email'
import JoinTerms from '@/components/page/JoinTerms'
import Join from '@/components/page/Join'
import InquiriesWriting from '@/components/page/InquiriesWriting'
import InquiriesTexting from '@/components/page/InquiriesTexting'
import CustomerTexting from '@/components/page/CustomerTexting'
import MyInformation from '@/components/page/MyInformation'
import MyPassword from '@/components/page/MyPassword'
import Withdrawal from '@/components/page/Withdrawal'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/shipment',
      name: 'Shipment',
      component: Shipment
    },
    {
      path: '/sentlist',
      name: 'SentList',
      component: SentList
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/address',
      name: 'Address',
      component: Address
    },
    {
      path: '/customerservice',
      name: 'CustomerService',
      component: CustomerService
    },
    {
      path: '/charging',
      name: 'Charging',
      component: Charging
    },
    {
      path: '/addressspam',
      name: 'AddressSpam',
      component: AddressSpam
    },
    {
      path: '/reservatione',
      name: 'Reservatione',
      component: Reservatione
    },
    {
      path: '/inquiries',
      name: 'Inquiries',
      component: Inquiries
    },
    {
      path: '/faq',
      name: 'Faq',
      component: Faq
    },
    {
      path: '/terms',
      name: 'Terms',
      component: Terms
    },
    {
      path: '/findid',
      name: 'FindId',
      component: FindId
    },
    {
      path: '/findpass',
      name: 'FindPass',
      component: FindPass
    },
    {
      path: '/patmentdetails',
      name: 'PatmentDetails',
      component: PatmentDetails
    },
    {
      path: '/userInfo',
      name: 'UserInfo',
      component: UserInfo
    },
    {
      path: '/spam',
      name: 'Spam',
      component: Spam
    },
    {
      path: '/personal',
      name: 'Personal',
      component: Personal
    },
    {
      path: '/email',
      name: 'Email',
      component: Email
    },
    {
      path: '/jointerms',
      name: 'JoinTerms',
      component: JoinTerms
    },
    {
      path: '/join',
      name: 'Join',
      component: Join
    },
    {
      path: '/inquirieswriting',
      name: 'InquiriesWriting',
      component: InquiriesWriting
    },
    {
      path: '/inquiriestexting',
      name: 'InquiriesTexting',
      component: InquiriesTexting
    },
    {
      path: '/customertexting',
      name: 'CustomerTexting',
      component: CustomerTexting
    },
    {
      path: '/myinformation',
      name: 'MyInformation',
      component: MyInformation
    },
    {
      path: '/mypassword',
      name: 'MyPassword',
      component: MyPassword
    },
    {
      path: '/withdrawal',
      name: 'Withdrawal',
      component: Withdrawal
    }
  ]
})



