<template>
  <div>
    <HeaderTop/>
    <Header/>
    <div class="csWrap">
        <div class="componentSection">
          <div>
            <SidemenuLogin/>
            <SidemenuSpam action="3"/>
            <SidemenuHelp/>
          </div>
            <div class="csContainer">
                <div class="csHeader">
                    <h2>이메일 무단수집거부</h2>
                </div>
                <div class="csInfo">
                    <p>본 웹사이트에 게시된 주소가 전자우편 수집 프로그램이나 그 밖의 기술적 장치를 이용하여 무단으로 수집되는 것을 거부하며 이를 위반 시 정보통신망법에 의해 형사처벌 됨을 유념하시기 바랍니다.</p>
                    &nbsp;
                    <p>정보통신망법 제50조2(전자우편주소의 무단 수집행위 등 금지)</p>
                    <p>누구든지 전자우편주소의 수집을 거부하는 의사가 명시된 인터넷 홈페이지에서 자동으로 전자우편 주소를 수집하는 프로그램이나 그 밖의 기술적 장치를 이용하여 전자우편 주소를 수집하여서는 아니된다.</p>
                    <p>누구든지 제1항의 규정을 위반하여 수집된 전자우편 주소를 판매ㆍ유통하여서는 아니된다.</p>
                    <p>누구든지 제1항 및 제2항의 규정에 의하여 수집ㆍ판매 및 유통이 금지된 전자우편주소 임을 알고 이를 정보전송에 이용하여서는 아니된다.</p>
                </div>
            </div>
        </div>
    </div>
    <Footer/>
  </div>
</template>

<script>
import Header from '../Header'
import HeaderTop from '../HeaderTop'
import Footer from '../Footer'
import SidemenuLogin from '../SidemenuLogin'
import SidemenuSpam from '../SidemenuSpam'
import SidemenuHelp from '../SidemenuHelp'

export default {
  name: 'Email',
  components: {
    Header,
    Footer,
    HeaderTop,
    SidemenuLogin,
    SidemenuSpam,
    SidemenuHelp
  },
  data () {
    return {}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
    outline: none;
    list-style: none;
}
.csWrap {
    width: 100%;
}
.csContainer {
    width: 820px;
}
.csHeader {
    border-bottom: 2px solid #ccc;
    padding-bottom: 7px;
    margin-top: 10px;
}
.csInfo {
    margin: 40px 0;
    line-height: 1.6;
    padding: 20px;
    font-size: 14px;
}
.csInfo > h3 {
    margin-top: 20px;
    border-bottom: 1px solid #ccc;
    padding-bottom: 5px;
}
.csInfo > h4 {
    margin-top: 20px;
    padding-bottom: 10px;
}
</style>



