<template>
<div>
    <HeaderTop/>
    <Header/>
    <div class="spamWrap">
        <div class="componentSection">
            <div>
                <SidemenuLogin/>
                <SidemenuList action="1"/>
                <SidemenuHelp/>
            </div>
            <div class="spamSection">
                <div class="spamSection__top">
                    <div class="spamSection__top--title">
                        <h2>수신차단관리</h2>
                    </div>
                    <div class="spamSection__top--alarm">
                        <ul>
                            <li>수신거부 번호(휴대폰 및 팩스)를 관리하실 수 있습니다.</li>
                            <li>수신거부에 입력된 휴대폰 리스트는 문자전송시 필터링 되어 전송되지 않습니다.</li>
                            <li>전송을 차단할 번호는 수신거부기능 보다 발송목록에서 제거 후 발송하는 것을 권장합니다.</li>
                            <li>웹문자는 수신거부 기능에 따른 어떤 민/형사상의 법적 책임도 지지 않습니다.</li>
                        </ul>
                    </div>
                    <div class="spamSection__top--option">
                        <div class="spamSection__top--option1">
                            <span>수신거부</span>
                            <div>등록된 수 :</div>
                            <button>수신거부번호 추가</button>
                        </div>
                        <div class="spamSection__top--option2">
                            <span>검색하기</span>
                            <input type="text" placeholder="검색어 입력">
                            <button>검색</button>
                        </div>
                    </div>
                </div>
                <div class="spamSection__bottom">
                    <div class="spamSection__bottom__check">
                        <div class="spamSection__bottom__check--left">
                            <button>전체선택</button>
                            <button>선택취소</button>
                            <button>선택삭제</button>
                            <button>수신차단번호 전체삭제</button>
                        </div>
                        <div class="spamSection__bottom__check--right">
                            <button>수신차단번호 다운로드</button>
                            <select onchange="page_list();">
                                <option value="20" sekected>20개</option>
                                <option value="30" sekected>30개</option>
                                <option value="50" sekected>50개</option>
                                <option value="100" sekected>100개</option>
                            </select>
                        </div>
                    </div>
                    <table class="spamSection__bottom__table">
                        <thead>
                            <tr>
                                <th class="th__right"><input type="checkbox"></th>
                                <th class="th__right">번호</th>
                                <th class="th__right">등록일</th>
                                <th class="th__right">이름</th>
                                <th>차단번호</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td colspan="5">수신차단 리스트가 없습니다.</td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    </div>
    <Footer/>
</div>
</template>

<script>
import Header from '../Header'
import HeaderTop from '../HeaderTop'
import Footer from '../Footer'
import SidemenuLogin from '../SidemenuLogin'
import SidemenuList from '../SidemenuList'
import SidemenuHelp from '../SidemenuHelp'

export default {
  name: 'AddressSpam',
  components: {
    Header,
    Footer,
    HeaderTop,
    SidemenuLogin,
    SidemenuList,
    SidemenuHelp
  },
  data () {
    return {
      menuIndex: 0
    }
  },
  methods: {
    menuChange (index) {
      this.menuIndex = index
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
    outline: none;
    list-style: none;
}
.spamWrap {
    width: 100%;
    height: 100%;
}
.componentSection {
    display: flex;
    width: 1100px;
    margin: 0 auto;
    justify-content: space-between;
}
.spamSection {
    width: 820px;
}
.spamSection__top--title {
    border-bottom: 2px solid #ccc;
    box-sizing: border-box;
    padding-bottom: 7px;
    margin-top: 10px;
}
.spamSection__top--alarm {
    width: 100%;
    border: 1px solid #ccc;
    box-sizing: border-box;
    padding: 10px 20px;
    margin: 10px 0px 20px;
}
.spamSection__top--option {
    display: flex;
    flex-direction: column;
}
.spamSection__top--option span {
    font-size: 16px;
    font-weight: 700;
}
.spamSection__top--option input {
    width: 230px;
    height: 30px;
    box-sizing: border-box;
    padding-left: 5px;
}
.spamSection__top--option1 {
    display: flex;
    align-items: center;
    box-sizing: border-box;
    padding: 15px 0px;
    border-bottom: 1px solid gray;
}
.spamSection__top--option1 > div {
    margin: 0px 10px 0px;
}
.spamSection__top--option1 > button {
    margin: 0px 10px 0px;
}
.spamSection__top--option2 {
    display: flex;
    align-items: center;
    box-sizing: border-box;
    padding: 15px 0px;
    border-bottom: 1px solid gray;
}
.spamSection__top--option2 input {
    margin: 0px 10px 0px;
}
.spamSection__bottom__check {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 15px;
}
.spamSection__bottom__check button {
    margin-right: 10px;
}
.spamSection__bottom__check--right select {
    box-sizing: border-box;
    padding: 5px 3px 5px;
}
.spamSection__bottom__table {
    width: 100%;
    height: 100%;
    border-collapse: collapse;
    margin-top: 15px;
}
.spamSection__bottom__table thead {
    width: 100%;
    height: 40px;
    background: #c9c9c9;
    border-top: 2px solid #000;
}
.th__right {
    border-right: 1px solid #000;
}
.spamSection__bottom__table tbody td {
    text-align: center;
    height: 200px;
    box-sizing: border-box;
    padding: 10px;
    border-bottom: 1px solid #000
}
</style>



