<template>
  <div>
    <div class="loginWrap">
      <div class="loginContainer">
        <div class="header">
          <a href="/">
            <img src="../../assets/img/logo.png">
          </a>
        </div>
        <div class="loginContent">
          <div>
            <input type="text" placeholder="아이디">
          </div>
          <div>
            <input type="password" placeholder="비밀번호">
          </div>
          <div>
            <button>로그인</button>
          </div>
          <div class="findInfo">
            <a @click="find">아이디/비밀번호 찾기</a>
            <a href="/#/jointerms">회원가입</a>
          </div>
        </div>
      </div>
    </div>
    <Footer/>
  </div>
</template>

<script>
import Footer from '../Footer'

export default {
  name: 'Login',
  components: {
    Footer
  },
  data () {
    return {
      menuIndex: 0
    }
  },
  methods: {
    find () {
      window.open('/#/FindId', 'a', 'width=500, height=600, left=700, top=200')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
    outline: none;
    list-style: none;
}
.loginWrap {
    width: 100%;
}
.loginContainer {
    width: 800px;
    margin: 0 auto;
    min-width: 800px;
}
.header {
    text-align: center;
    padding: 50px 0;
}
.loginContent {
    width: 800px;
    margin: 0 auto;
    text-align: center;
}
.loginContent > div > input {
    width: 380px;
    height: 40px;
    margin-top: 10px;
    padding-left: 10px;
}
.loginContent > div > button {
    width: 395px;
    height: 43px;
    margin-top: 10px;
    border: 0;
    color: #fff;
    font-size: 20px;
    font-weight: bold;
    background-color: #009e4f;
}
.findInfo {
    margin: 20px 0;
}
.findInfo a {
    margin-right: 25px;
    cursor: pointer;
    color: #000;
}
</style>



