<template>
  <div>
    <HeaderTop/>
    <Header/>
    <div class="csWrap">
        <div class="componentSection">
          <div>
            <SidemenuAfterLogin/>
            <!--<SidemenuLogin/>-->
            <SidemenuListMyInfo action="2"/>
            <SidemenuHelp/>
          </div>
            <div class="csContainer">
                <div class="csHeader">
                    <h2>회원탈퇴</h2>
                </div>
                <div class="csInfo">
                    <p>*회원탈퇴 전 해당 내용을 반드시 확인하신 후 탈퇴하시기 바랍니다.</p>
                </div>
                <div class="writingWrap">
                    <h4>회원탈퇴 전 확인사항</h4>
                    <table>
                        <tr>
                            <th>회원정보</th>
                            <td>전자상거래 소비자 보호법에 의거한 개인정보보호정책에 따라 관리되며 <br>회원탈퇴 시 회원정보. 주소록 및 저장데이터는 자동 삭제됩니다.</td>
                        </tr>
                        <tr>
                            <th>잔여금액</th>
                            <td>잔여건수 또는 잔여금액 모두 삭제</td>
                        </tr>
                        <tr>
                            <th>회원탈퇴 불가능</th>
                            <td>서비스 이용 요금 미납 시 탈퇴 불가능</td>
                        </tr>
                    </table>
                    <h4>재가입 유예기간</h4>
                    <table>
                        <tr>
                            <th>일반</th>
                            <td>1개월 이내 (거래내역 유무 상관없음)</td>
                        </tr>
                        <tr>
                            <th>예외</th>
                            <td>사이트 이용 차단 내역 있는 회원: 6개월 / 회원자격 영구 정지 처리된 회원: 재가입 불가능</td>
                        </tr>
                    </table>
                    <table>
                        <tr>
                            <th>탈퇴사유</th>
                            <td>
                                <input type="radio" name="text" id="test1">
                                <label for="test1">서비스 이용 불편</label>
                                <input type="radio" name="text" id="test2">
                                <label for="test2">이용 요금</label>
                                <input type="radio" name="text" id="test3">
                                <label for="test3">더이상 이용하지않음</label><br>
                                <input type="radio" name="text" id="test4">
                                <label for="test4">고객 상담 불만</label>
                                <input type="radio" name="text" id="test5">
                                <label for="test5">원하는 서비스가 미지원</label>
                                <input type="radio" name="text" id="test6">
                                <label for="test6">타 사이트로 변경</label>
                            </td>
                        </tr>
                        <tr>
                            <th>기타</th>
                            <td><textarea rows="5"></textarea></td>
                        </tr>
                        <tr>
                            <th>비밀번호 입력</th>
                            <td><input type="password"></td>
                        </tr>
                    </table>
                    <div class="buttonBox">
                        <button>회원탈퇴</button>
                        <button>취소</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <Footer/>
  </div>
</template>

<script>
import Header from '../Header'
import HeaderTop from '../HeaderTop'
import Footer from '../Footer'
import SidemenuLogin from '../SidemenuLogin'
import SidemenuListMyInfo from '../SidemenuListMyInfo'
import SidemenuHelp from '../SidemenuHelp'
import SidemenuAfterLogin from '../SidemenuAfterLogin'

export default {
  name: 'Withdrawal',
  components: {
    Header,
    Footer,
    HeaderTop,
    SidemenuLogin,
    SidemenuAfterLogin,
    SidemenuListMyInfo,
    SidemenuHelp
  },
  data () {
    return {}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
    outline: none;
    list-style: none;
}
.writingWrap > table > tr > td > input {
    margin-left: 10px;
}
textarea {
    width: 90%;
    margin-top: 15px;
    padding: 5px;
}
.csWrap {
    width: 100%;
}
.csContainer {
    width: 820px;
}
.csHeader {
    border-bottom: 2px solid #ccc;
    box-sizing: border-box;
    padding-bottom: 7px;
    margin-top: 10px;
}
.csInfo {
    width: 100%;
    border: 1px solid #ccc;
    box-sizing: border-box;
    padding: 10px 20px;
    margin: 10px 0px 20px;
}
.writingWrap > h4 {
    padding-bottom: 10px;
    margin-top: 20px;
}
.writingWrap > table {
    width: 100%;
    border-collapse: collapse;
    line-height: 3;
}
.writingWrap > table > tr > th {
    width: 200px;
    border:1px solid #e3e3e3;
    background-color: #f6f5ff;
    font-size: 14px;
}
.writingWrap > table > tr > td {
    border:1px solid #e3e3e3;
    padding-left: 15px;
    font-size: 15px;
}
.buttonBox {
    margin-top: 20px;
    text-align: center;
}
.buttonBox > button {
    width: 150px;
    height: 50px;
}
</style>



