<template>
<div>
    <div class="sidemenu__login">
        <div class="sidemenu__login__top">
            <span>LOGIN</span>
            <input type="text" placeholder="아이디">
            <input type="text" placeholder="비밀번호입력">
        </div>
        <div class="sidemenu__login__middle">
            <input type="checkbox">
            <span>아이디저장</span>
        </div>
        <div class="sidemenu__login__bottom">
            <button>로그인</button>
            <button @click="find">ID/PW 찾기</button>
        </div>
    </div>
    <div class="sidemenu__banner">
        <img @click="$router.push('JoinTerms')" src="https://www.webmunja.co.kr/img/common/leftBanner_join.png">
    </div>
</div>
</template>

<script>

export default {
  name: 'SidemenuLogin',
  data () {
    return {
      menuIndex: 0
    }
  },
  methods: {
    find () {
      window.open('/#/FindId', 'a', 'width=500, height=600, left=700, top=200')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
    outline: none;
    list-style: none;
}
.wrap {
    width: 100%;
    height: 100%;
}
.sidemenu {
    width: 250px;
    height: 100%;
}
.sidemenu__login {
    border: 1px solid #000;
    box-sizing: border-box;
    padding: 15px;
    margin-top: 10px;
}
.sidemenu__login__top {
    display: flex;
    flex-direction: column;
    text-align: center;
}
.sidemenu__login__top > span {
    margin-bottom: 15px;
    font-weight: 700;
}
.sidemenu__login__top > input {
    height: 35px;
    margin: 3px 0px;
    box-sizing: border-box;
    padding: 5px;
}
.sidemenu__login__middle {
    display: flex;
    align-items: center;
    margin: 5px 0px;
    font-size: 14px;
}
.sidemenu__login__middle span {
    margin-left: 5px;
}
.sidemenu__login__bottom {
    display: flex;
    flex-direction: column;
}
.sidemenu__login__bottom button {
    height: 35px;
    margin: 3px 0px;
}
.sidemenu__banner > img {
    width: 100%;
    height: 102px;
    margin-top: 15px;
}
</style>



