<template>
<div>
  <div class="header__gnb">
    <div class="header__gnb__nav">
      <ul class="header__gnb__nav--list">
        <img src="../assets/img/logo.png" @click="home">
        <li>
          <a href="/#/shipment">문자발송</a>
        </li>
        <li @mouseover="listOne = true" @mouseleave="listOne = false">
          <a href="/#/address">주소록</a>
          <transition name="fade">
            <ul v-if="listOne" @click="listOne = false">
              <li><a href="/#/address">주소록 관리</a></li>
              <li><a href="/#/addressspam">수신차단관리</a></li>
            </ul>
          </transition>
        </li>
        <li @mouseover="listTwo = true" @mouseleave="listTwo = false">
          <a href="/#/sentList">발송함</a>
          <transition name="fade">
            <ul v-if="listTwo" @click="listTwo = false">
              <li><a href="/#/sentList">발송내역</a></li>
              <li><a href="/#/Reservatione">예약내역</a></li>
            </ul>
          </transition>
        </li>
        <li><a href="/#/charging">충전소</a></li>
        <li @mouseover="listThree = true" @mouseleave="listThree = false">
          <a href="/#/customerservice">고객센터</a>
          <transition name="fade">
            <ul v-if="listThree" @click="listThree = false">
              <li><a href="/#/customerservice">공지사항</a></li>
              <li><a href="/#/Inquiries">1:1문의</a></li>
              <li><a href="/#/Faq">자주묻는질문</a></li>
              <li><a href="/#/Terms">이용약관</a></li>
            </ul>
          </transition>
        </li>
      </ul>
    </div>
  </div>
</div>
</template>

<script>
export default {
  name: 'Header',
  data () {
    return {
      listOne: false,
      listTwo: false,
      listThree: false
    }
  },
  methods: {
    home: function () {
      this.$router.push('/')
    }
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.header__gnb {
  background-color: #c1c1c1;
  border: 1px solid #ccc;
}
.header__gnb__nav {
  width: 1200px;
  height: 55px;
  margin: 0 auto;
}
.header__gnb__nav--list {
  display: flex;
  justify-content: space-evenly;
  list-style: none;
  height: 100%;
  align-items: center;
  font-size: 20px;
  font-weight: bold;
}
.header__gnb__nav--list > li {
  height: 100%;
  display: flex;
  align-items: center;
}
.header__gnb__nav--list > li > a {
  color: #000;
  position: relative;
  text-transform: uppercase;
  transition: 0.5s;
}
.header__gnb__nav--list > li > a::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 3px;
  top: 100%;
  left: 0;
  background: #ff7000;
  transition: transform 0.5s;
  transform: scaleX(0);
  transform-origin: right;
}
.header__gnb__nav--list > li > a:hover {
  color: #95a5a6;
}
.header__gnb__nav--list > li > a:hover::after {
  transform: scaleX(1);
  transform-origin: left;
}
.header__gnb__nav--list > img {
  width: 100px;
}
.header__gnb__nav--list > li {
  position: relative;
}
.header__gnb__nav--list > li > ul {
  position: absolute;
  width: 130px;
  left: 0;
  top: 101%;
  margin: 0;
  padding: 0;
  font-size: 16px;
  z-index: 1;
}
.header__gnb__nav--list > li > ul > li {
  background: #c1c1c1;
  transition: background .2s;
  list-style: none;
  box-sizing: border-box;
  padding: 10px 5px;
}
.header__gnb__nav--list > li > ul > li:hover {
  background: #fff;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .2s;
}
.fade-enter, .fade-leave-active {
  opacity: 0;
}
</style>



