<template>
<div>
    <div class="shipmentContainer__top--left--phone">
        <div class="phoneTitle"><input type="text" placeholder="제목입력"></div>
        <div class="textBox">
            <a href="#">이미지입력</a>
            <textarea class="text" cols="49" rows="11" placeholder="MMS"></textarea>
        </div>
        <div><button>다시 쓰기</button><button>자주쓰는 메세지</button></div>
        <div>
          발신번호
        <select>
            <option>010-5513-1549</option>
            <option>112</option>
        </select>
        </div>
        <div>예약전송<input type="checkbox" value="yyy">
        <input class="timeInfoinput" type="text" value="날짜/달력">
        <select>
        <option v-for="m in 24" v-bind:key="m" :value="m">{{ 10 > m ? '0' + m + '시' : m + '시' }}</option>
        </select>
        <select>
        <option v-for="s in 59" v-bind:key="s" :value="s">{{ 10 > s ? '0' + s + '분' : s + '분' }}</option>
        </select>
        </div>
        <div><button class="sendButton">문자 전송하기</button></div>
    </div>
</div>
</template>

<script>
export default {
  name: 'Mms',
  data () {
    return {}
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.timeInfoinput {
  width: 70px;
}
.sendButton {
  width: 390px;
  height: 60px;
}
.shipmentContainer__top--left {
  width: 50%;
  height: 600px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.shipmentContainer__top--left--phone {
  max-width: 400px;
  height: 500px;
  border: 1px solid #ccc;
}
.shipmentContainer__top--left--phoneButton {
  width: 20%;
  max-width: 110px;
  height: 400px;
}
.shipmentContainer__top--left--phoneButton button {
  width: 65px;
  height: 40px;
  border-left: 0;
  background: #fff;
}
.shipmentContainer__top--left--phoneButton button.active  {
  width: 80px;
}
.phoneTitle {
  padding-left: 15px;
  font-size: 20px;
  border-bottom: 1px solid #ccc;
}
.phoneTitle > input {
  width: 375px;
  height: 25px;
  border: 0;
  font-size: 17px
}
.textBox {
    text-align: center;
    height: 335px;
}
.textBox > a {
    display: block;
    padding: 32px 0;
}
.text {
  border: 0;
  padding: 15px 15px 22px;
  background-color: #e3f6fe;
}
</style>



