<template>
<div class="nameSection">
    <div class="nameSection__box">
        <button @click="adduction">본인명의 휴대폰 인증</button>
    </div>
</div>
</template>

<script>

export default {
  name: 'FindName',
  components: {
  },
  data () {
    return {
    }
  },
  methods: {
    adduction () {
      window.open('https://www.kmcert.com/kmcis/web_v3/kmcisHp00.jsp', 'b', 'width=420, height=540, left=400, top=100')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
    outline: none;
    list-style: none;
}
.nameSection__box {
    display: flex;
    width: 100%;
    justify-content: center;
    border: 1px solid #c9c9c9;
    border-radius: 10px;
    padding: 70px 0px;
    background: #fafbfd;
}
.nameSection__box button {
    width: 80%;
    height: 60px;
    border-radius: 15px;
    background: #009e4f;
    color: #fff;
    border: 0px;
    font-size: 23px;
    cursor: pointer;
}
</style>



