<template>
<div class="sidemenu__help">
    <img src="https://www.webmunja.co.kr/img/common/cs_tel.png">
    <ul class="sidemenu__help__top">
        <li>
            <span>FAX</span>
            02-0000-0000
        </li>
        <li>itechcompany@naver.com</li>
    </ul>
    <ul class="sidemenu__help__bottom">
        <li>평일 00:00 ~ 00:00</li>
        <li>점심 00:00 ~ 00:00</li>
        <li>토ㆍ일요일, 공휴일 휴무</li>
    </ul>
    <button type="button" onclick="location.href='/#/InquiriesWriting'">문의하기</button>
</div>
</template>

<script>
export default {
  name: 'SidemenuHelp',
  data () {
    return {
      menuIndex: 0
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
    outline: none;
    list-style: none;
}
.sidemenu__help {
    display: flex;
    flex-direction: column;
    border: 1px solid gray;
    box-sizing: border-box;
    margin-top: 15px;
    padding: 15px;
}
.sidemenu__help img {
    width: 80%;
    height: 82px;
    margin: auto;
}
.sidemenu__help__top {
    margin-top: 10px;
    box-sizing: border-box;
    padding: 10px 0px;
}
.sidemenu__help__top > li > span {
    color: hotpink;
    font-weight: 600;
}
.sidemenu__help__bottom {
    border-top: 1px solid gray;
    box-sizing: border-box;
    padding: 10px 0px;
}
.sidemenu__help button {
    height: 30px;
}
</style>



