<template>
  <div>
    <HeaderTop/>
    <div class="wrap">
        <div class="article">
            <swiper class="swiper" :options="swiperOption__1">
                <swiper-slide class="swiperSlide">
                    <div class="slide__item">
                        <h1><a href="/#/shipment">문자발송</a></h1>
                    </div>
                </swiper-slide>
                <swiper-slide class="swiperSlide">
                    <div class="slide__item">
                      <h1><a href="/#/address">주소록</a></h1>
                    </div>
                </swiper-slide>
                <swiper-slide class="swiperSlide">
                    <div class="slide__item">
                      <h1><a href="/#/sentList">발송함</a></h1>
                    </div>
                </swiper-slide>
                <swiper-slide class="swiperSlide">
                    <div class="slide__item">
                        <h1><a href="/#/charging">충전소</a></h1>
                    </div>
                </swiper-slide>
            </swiper>
            <swiper class="swiper__banner" :options="swiperOption__2">
                <swiper-slide class="swiperSlide__banner">
                    <div class="slide__banner">
                        <img src="https://static.welaaa.co.kr/images/20606-6-1622003027838.jpg">
                    </div>
                </swiper-slide>
                <swiper-slide class="swiperSlide__banner">
                    <div class="slide__banner">
                        <img src="https://static.welaaa.co.kr/images/20606-6-1622003027838.jpg">
                    </div>
                </swiper-slide>
                <swiper-slide class="swiperSlide__banner">
                    <div class="slide__banner">
                        <img src="https://static.welaaa.co.kr/images/20606-6-1622003027838.jpg">
                    </div>
                </swiper-slide>
            </swiper>
        </div>
    </div>
    <Footer/>
  </div>
</template>

<script>
import Header from '../Header'
import HeaderTop from '../HeaderTop'
import Footer from '../Footer'
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import 'swiper/css/swiper.css'

export default {
  name: 'Index',
  components: {
    Header,
    Footer,
    HeaderTop,
    Swiper,
    SwiperSlide
  },
  data () {
    return {
      swiperOption__1: {
        slidesPerView: 3,
        speed: 400,
        centeredSlides: true,
        loop: true,
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        },
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        }
      },
      swiperOption__2: {
        slidesPerView: 1.5,
        spaceBetween: 20,
        centeredSlides: true,
        loop: true,
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        },
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        }
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.article {
  width: 100%;
  height: 100%;
}
.swiper {
  width: 100%;
  height: 100%;
}
.swiperSlide {
  display: flex;
  height: 400px;
  justify-content: center;
  align-items: center;
}
.slide__item {
  display: flex;
  width: 45%;
  height: 85%;
  background-color: #ebebeb;
  border-radius: 5%;
  justify-content: center;
  align-items: center;
}
.slide__item a {
  color: #000;
}
.swiper__banner {
  display: flex;
  justify-content: center;
  margin-top: 50px;
}
.swiperSlide__banner {
  display: flex;
  justify-content: center;
}
.article > div:nth-child(1) .swiperSlide.swiper-slide {
  transition: all 0.5s;
}

.article > div:nth-child(1) .swiperSlide.swiper-slide:hover {
  padding: 0px;
  transform: scale(1.1);
}

/* .article > div:nth-child(1) .swiperSlide.swiper-slide.swiper-slide-active{
  padding: 0px;
  transform: scale(1.3);
} */
</style>



