<template>
<div class="wrap">
    <div class="section">
        <h2>계정정보 찾기</h2>
        <nav class="section__select">
            <ul>
                <li>
                    <a class="section__select--id" href="/#/findid">아이디 찾기</a>
                </li>
                <li>
                    <a class="section__select--pass" href="/#/findpass">비밀번호 찾기</a>
                </li>
            </ul>
        </nav>
        <div class="section__box">
            <div class="section__box__span">
                <span>비밀번호의 경우 <strong>암호화</strong> 저장되어 분실 시 찾아드릴 수 없는 정보입니다.</span><br>
                <span><strong>본인 확인</strong>을 통해 비밀번호를 재설정 하실 수 있습니다.</span>
            </div>
            <table class="passSection__box">
                <tr>
                    <th>이름</th>
                    <td><input type="text"></td>
                </tr>
                <tr>
                    <th>아이디</th>
                    <td>
                        <input type="text">
                        <br><span>가입된 아이디를 정확히 기입해주시길 바랍니다.</span>
                    </td>
                </tr>
            </table>
            <div class="passSection__box__button">
                <button @click="alarm">비밀번호 찾기</button>
            </div>
        </div>
    </div>
</div>
</template>

<script>

export default {
  name: 'FindPass',
  components: {
  },
  data () {
    return {
    }
  },
  methods: {
    alarm () {
      alert('준비 중입니다')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
    outline: none;
    list-style: none;
}
.wrap {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
}
.section {
    width: 450px;
    height: 100%;
    box-sizing: border-box;
}
.section > h2 {
    margin: 15px 0px;
}
.section__select {
    border-bottom: 2px solid #009e4f;
}
.section__select ul {
    display: flex;
}
.section__select ul li {
    width: 50%;
}
.section__select a {
    display: block;
    position: relative;
    background: #fff;
    color: #000;
    padding: 10px 0px;
    text-align: center;
    margin-bottom: -2px;
    cursor: pointer;
}
.section__select--pass {
    border-left: 2px solid #009e4f;
    border-top: 2px solid #009e4f;
    border-right: 2px solid #009e4f;
}
.section__box {
    justify-content: center;
}
.section__box__span {
    margin: 15px 0px;
}
.section__box__span span {
    font-size: 14px;
}
.passSection__box {
  width: 100%;
  justify-content: center;
  border-collapse: collapse;
  border-top: 2px solid gray;
}
.passSection__box th {
  width: 110px;
  text-align: left;
  box-sizing: border-box;
  padding: 10px;
  background: #ebebeb;
  border-right: 1px solid gray;
  border-bottom: 1px solid gray;
}
.passSection__box td {
  width: 340px;
  border-bottom: 1px solid gray;
  padding: 10px;
}
.passSection__box td > span {
    font-size: 13px;
}
.passSection__box input {
  width: 65%;
  box-sizing: border-box;
  padding:0px 5px;
}
.section__box button {
    margin-top: 15px;
    width: 140px;
    height: 50px;
    border-radius: 15px;
    background: #009e4f;
    color: #fff;
    border: 0px;
    font-size: 18px;
    cursor: pointer;
}
.passSection__box__button {
    width: 100%;
    display: flex;
    justify-content: center;
}
</style>



