<template>
  <div>
    <HeaderTop/>
    <Header/>
    <div class="csWrap">
        <div class="componentSection">
          <div>
            <SidemenuLogin/>
            <SidemenuListCustomer action="0"/>
            <SidemenuHelp/>
          </div>
            <div class="csContainer">
                <div class="csHeader">
                    <h2>공지안내</h2>
                </div>
                <div class="writingWrap">
                    <table>
                        <tr class="title">
                            <th>제목</th>
                            <td>점검안내</td>
                        </tr>
                        <tr class="content">
                            <th>내용</th>
                            <td>안녕하세요 고객님, 문자마트 입니다.
                            항상 저희 문자마트를 이용해 주셔서 감사드립니다.
                            DB 서버 점검 작업으로 인하여
                            작업시간대에 웹사이트 접속이 불안정함을 알려드립니다.
                            - 일시 : 2021.6.14(월) 19:00 ~21:00 (2시간)
                            - 서비스 영향 : 사이트 접속 순단 현상 및 문자메시지 발송 전송 순단
                            (예약된 메시지는 정상적으로 발송)
                            사전에 미리 알려드리오니, 위 내용을 꼭 참고하시어 문자발송에 유의해주시기 바랍니다.
                            감사합니다.</td>
                        </tr>
                        <tr>
                            <th>작성일자</th>
                            <td>2021-06-10</td>
                        </tr>
                    </table>
                    <div class="buttonBox">
                        <button type="button" onclick="location.href='/#/CustomerService'">목록으로</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <Footer/>
  </div>
</template>

<script>
import Header from '../Header'
import HeaderTop from '../HeaderTop'
import Footer from '../Footer'
import SidemenuLogin from '../SidemenuLogin'
import SidemenuListCustomer from '../SidemenuListCustomer'
import SidemenuHelp from '../SidemenuHelp'

export default {
  name: 'CustomerTexting',
  components: {
    Header,
    Footer,
    HeaderTop,
    SidemenuLogin,
    SidemenuListCustomer,
    SidemenuHelp
  },
  data () {
    return {}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
    outline: none;
    list-style: none;
}
.csWrap {
    width: 100%;
}
.csContainer {
    width: 820px;
}
.csHeader {
    border-bottom: 2px solid #ccc;
    padding-bottom: 7px;
    margin-top: 10px;
}
.writingWrap {
    border-top: 2px solid #e3e3e3;
    margin-top: 40px;
}
.writingWrap > table{
    line-height: 2.5;
    border-collapse: collapse;
    width: 100%;
}
.writingWrap > table > tr > th {
    width: 170px;
    border-right: 1px solid #e3e3e3;
    border-bottom: 1px solid #e3e3e3;
}
.writingWrap > table > tr > td {
    padding-left: 15px;
    border-bottom: 1px solid #e3e3e3;
}
.content > th {
    height: 400px;
}
.buttonBox {
    margin-top: 20px;
    display: flex;
    justify-content: center;
}
.buttonBox > button {
    width: 100px;
    height: 40px;
    margin-left: 10px;
}
</style>



