<template>
<div class="numberSection">
    <table class="numberSection__box">
        <tr>
          <th>이름</th>
          <td><input type="text"></td>
        </tr>
        <tr>
          <th>휴대폰 번호</th>
          <td>
            <input type="text">
            <button>인증번호 전송</button>
          </td>
        </tr>
    </table>
</div>
</template>

<script>

export default {
  name: 'FindNumber',
  components: {
  },
  data () {
    return {
    }
  },
  methods: {
    adduction () {
      window.open('https://www.kmcert.com/kmcis/web_v3/kmcisHp00.jsp', 'b', 'width=420, height=540, left=400, top=100')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
    outline: none;
    list-style: none;
}
.numberSection__box {
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  border-collapse: collapse;
  border-top: 2px solid gray;
}
.numberSection__box th {
  width: 110px;
  text-align: left;
  box-sizing: border-box;
  padding: 10px;
  background: #ebebeb;
  border-right: 1px solid gray;
  border-bottom: 1px solid gray;
}
.numberSection__box td {
  width: 280px;
  border-bottom: 1px solid gray;
  padding: 10px;
}
.numberSection__box input {
  width: 65%;
  box-sizing: border-box;
  padding:0px 5px;
}
</style>



