<template>
<div class="wrap">
    <div class="section">
        <h2>계정정보 찾기</h2>
        <nav class="section__select">
            <ul>
                <li>
                    <a class="section__select--id" href="/#/findid">아이디 찾기</a>
                </li>
                <li>
                    <a class="section__select--pass" href="/#/findpass">비밀번호 찾기</a>
                </li>
            </ul>
        </nav>
        <div class="section__box">
            <div class="section__box__select">
                <div>
                    <input type="radio" name="select" id="select1" value='1' v-model="name">
                    <label for="select1">본인명의 휴대폰 번호로 찾기</label>
                </div>
                <div>
                    <input type="radio" name="select" id="select2" value='2' v-model="name">
                    <label for="select2">등록된 휴대폰 번호로 찾기</label>
                </div>
            </div>
            <div class="section__box__contents">
                <FindName v-if="name === '1'"/>
                <FindNumber v-if="name === '2'"/>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import FindName from '../FindName'
import FindNumber from '../FindNumber'

export default {
  name: 'FindId',
  components: {
    FindName,
    FindNumber
  },
  data () {
    return {
      name: '1'
    }
  },
  methods: {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
    outline: none;
    list-style: none;
}
.wrap {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
}
.section {
    width: 450px;
    height: 100%;
    box-sizing: border-box;
}
.section > h2 {
    margin: 15px 0px;
}
.section__select {
    border-bottom: 2px solid #009e4f;
}
.section__select ul {
    display: flex;
}
.section__select ul li {
    width: 50%;
}
.section__select a {
    display: block;
    position: relative;
    background: #fff;
    color: #000;
    padding: 10px 0px;
    text-align: center;
    margin-bottom: -2px;
    cursor: pointer;
}
.section__select--id {
    border-left: 2px solid #009e4f;
    border-top: 2px solid #009e4f;
    border-right: 2px solid #009e4f;
}
.section__select--pass {
   /* border-left: 2px solid gray;
    border-top: 2px solid gray;
    border-right: 2px solid gray; */
}
.section__box {
    padding: 30px;
}
.section__box__select {
    display: flex;
    flex-direction: column;
}
.section__box__contents {
    margin-top: 30px;
}
</style>



