import Footer from '../Footer'
import SidemenuLogin from '../SidemenuLogin'
import SidemenuList from '../SidemenuList'
import SidemenuHelp from '../SidemenuHelp'

export default {
  name: 'Address',
  components: {
    Header,
    Footer,
    HeaderTop,
    SidemenuLogin,
    SidemenuList,
    SidemenuHelp
  },
  data () {
    return {
      menuIndex: 0
    }
  },
  methods: {
    menuChange (index) {
