<template>
  <div>
    <HeaderTop/>
    <Header/>
    <div class="chargingWrap">
      <div class="componentSection">
        <div>
          <SidemenuLogin/>
          <SidemenuListCharging action="0"/>
          <SidemenuHelp/>
        </div>
        <div class="chargingContainer">
            <div class="chargingHeader">
                <h2>충전하기</h2>
            </div>
            <div class="chargingInfo">
                <p>무통장/가상계좌이체는 계좌로 직접 입금을 해주셔야 충전처리가 됩니다.</p>
                <p>신용카드/휴대폰/KT폰빌(전화)결제는 실시간 충전이 가능합니다.</p>
                <p>계산서 발행은 부통장/가상계좌이체만 가능합니다. 영수증은'충전>내결제내역'에서 출력하실 수 있습니다.</p>
                <p>결제금액은 VAP 포함 금액(단문.장문.사진 단가는 VAP별도가)입니다.</p>
                <p>가상계좌는 일회성 계좌로 마감일까지만 입금처리됩니다.</p>
            </div>
            <div class="chargingNav">
                <button :class="{'active': menuIndex === 0 }" @click="menuChange(0)">가상계좌이체</button>
                <button :class="{'active': menuIndex === 1 }" @click="menuChange(1)">무통장입금</button>
                <button :class="{'active': menuIndex === 2 }" @click="menuChange(2)">신용카드결제</button>
                <button :class="{'active': menuIndex === 3 }" @click="menuChange(3)">휴대폰결제</button>
            </div>
            <div v-if="menuIndex === 0">
              <Virtual/>
            </div>
            <div v-else-if="menuIndex === 1">
              <NoBankBook/>
            </div>
            <div v-else-if="menuIndex === 2">
              <CreditCard/>
            </div>
            <div v-else-if="menuIndex === 3">
              <PhonePay/>
            </div>
        </div>
      </div>
    </div>
    <Footer/>
  </div>
</template>

<script>
import Header from '../Header'
import HeaderTop from '../HeaderTop'
import Footer from '../Footer'
import Virtual from '../Virtual'
import NoBankBook from '../NoBankBook'
import CreditCard from '../CreditCard'
import PhonePay from '../PhonePay'
import SidemenuLogin from '../SidemenuLogin'
import SidemenuListCharging from '../SidemenuListCharging'
import SidemenuHelp from '../SidemenuHelp'

export default {
  name: 'Charging',
  components: {
    Header,
    Footer,
    HeaderTop,
    Virtual,
    NoBankBook,
    CreditCard,
    PhonePay,
    SidemenuLogin,
    SidemenuListCharging,
    SidemenuHelp
  },
  data () {
    return {
      menuIndex: 0
    }
  },
  methods: {
    menuChange (index) {
      this.menuIndex = index
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
    outline: none;
    list-style: none;
}
.chargingWrap {
    width: 100%;
}
.componentSection {
    width: 1100px;
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
}
.chargingContainer {
    width: 820px;
}
.chargingHeader {
  border-bottom: 2px solid #ccc;
  box-sizing: border-box;
  padding-bottom: 7px;
  margin-top: 10px;
}
.chargingInfo {
  border: 1px solid #ccc;
  box-sizing: border-box;
  padding: 10px 20px;
  margin: 10px 0px 20px;
}
.chargingInfo > p {
    font-size: 15px;
    line-height: 2;
}
.chargingNav {
    display: flex;
    margin: 20px 0;
}
.chargingNav > button {
    width: 220px;
    height: 50px;
}
</style>



