<template>
  <div>
    <HeaderTop/>
    <Header/>
    <div class="csWrap">
        <div class="componentSection">
          <div>
            <SidemenuAfterLogin/>
            <SidemenuSpam action="1"/>
            <SidemenuHelp/>
          </div>
            <div class="csContainer">
                <div class="csHeader">
                    <h2>개인정보 취급방침</h2>
                </div>
                <div class="csInfo">
                    <h4>수집하는 개인정보 항목</h4>
                    <p>회사는 개인정보취급방침을 통하여 고객님께서 제공하시는 개인정보가 어떠한 용도와 방식으로 이용되고 있으며, 개인정보보호를 위해 어떠한 조치가 취해지고 있는지 알려드립니다.</p>
                    &nbsp;
                    <h4>회사는 수집한 개인정보를 다음의 목적을 위해 활용합니다..</h4>
                    <p>ο 서비스 제공에 관한 계약 이행 및 서비스 제공에 따른 요금정산</p>
                    <p>ο 콘텐츠 제공 , 구매 및 요금 결제 , 물품배송 또는 청구지 등 발송</p>
                    <p>ο 회원 관리</p>
                    &nbsp;
                    <h4> 개인정보의 파기절차 및 방법</h4>
                    <p>- 회사는 이용자의 개인정보를 원칙적으로 외부에 제공하지 않습니다. 다만, 아래의 경우에는 예외로 합니다.</p>
                    <p>- 야간 시간대 휴대폰광고는 전송이 금지됩니다.(21시~8시), 야간 시간대 발송하실 경우 추가동의를 받으셔야 합니다.</p>
                    <p>- 회사는 고객님의 동의없이 고객님의 정보를 외부 업체에 위탁하지 않습니다. 향후 그러한 필요가 생길 경우, 위탁 대상자와 위탁 업무 내용에 대해 고객님에게 통지하고 필요한 경우 사전 동의를 받도록 하겠습니다.</p>
                    <p>-  전자적 파일형태로 저장된 개인정보는 기록을 재생할 수 없는 기술적 방법을 사용하여 삭제합니다.</p>
                    <p>- 휴대폰 문자광고는 "[광고]" 문구와 무료 수신거부번호 080번호를 꼭 기입하셔야 합니다.</p>
                    &nbsp;
                    <h4>개인정보의 보유 및 이용기간</h4>
                    <p>회원님이 회원가입 등을 위해 입력하신 정보는 목적이 달성된 후 별도의 DB로 옮겨져(종이의 경우 별도의 서류함) 내부 방침 및 기타 관련 법령에 의한 정보보호 사유에 따라(보유 및 이용기간 참조) 일정 기간 저장된 후 파기되어집니다.</p>
                    <p>별도 DB로 옮겨진 개인정보는 법률에 의한 경우가 아니고서는 보유되어지는 이외의 다른 목적으로 이용되지 않습니다.</p>
                </div>
            </div>
        </div>
    </div>
    <Footer/>
  </div>
</template>

<script>
import Header from '../Header'
import HeaderTop from '../HeaderTop'
import Footer from '../Footer'
import SidemenuAfterLogin from '../SidemenuAfterLogin'
import SidemenuLogin from '../SidemenuLogin'
import SidemenuSpam from '../SidemenuSpam'
import SidemenuHelp from '../SidemenuHelp'

export default {
  name: 'Personal',
  components: {
    Header,
    Footer,
    HeaderTop,
    SidemenuLogin,
    SidemenuAfterLogin,
    SidemenuSpam,
    SidemenuHelp
  },
  data () {
    return {}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
    outline: none;
    list-style: none;
}
.csWrap {
    width: 100%;
}
.csContainer {
    width: 820px;
}
.csHeader {
    border-bottom: 2px solid #ccc;
    padding-bottom: 7px;
    margin-top: 10px;
}
.csInfo {
    margin: 40px 0;
    line-height: 1.6;
    padding: 20px;
    font-size: 14px;
}
.csInfo > h3 {
    margin-top: 20px;
    border-bottom: 1px solid #ccc;
    padding-bottom: 5px;
}
.csInfo > h4 {
    margin-top: 20px;
    padding-bottom: 10px;
}
</style>



