<template>
  <div>
    <HeaderTop/>
    <Header/>
    <div class="csWrap">
        <div class="componentSection">
          <div>
            <SidemenuLogin/>
            <SidemenuListCustomer action="2"/>
            <SidemenuHelp/>
          </div>
            <div class="csContainer">
                <div class="csHeader">
                    <h2>1:1 문의</h2>
                </div>
                <div class="csInfo">
                    <p>*문의사항 및 요청사항을 남겨주시면 답변드리겠습니다.</p>
                    <p>*주소록 대행입력 및 발송대행 서비스에 대한 요청도 가능합니다.</p>
                </div>
                <div class="writingWrap">
                    <table>
                        <tr>
                            <th>분류</th>
                            <td>
                                <select>
                                    <option>
                                        문의내용 선택하기
                                    </option>
                                    <option>
                                        문자전송
                                    </option>
                                    <option>
                                        전송관리
                                    </option>
                                    <option>
                                        발신번호
                                    </option>
                                    <option>
                                        주소록
                                    </option>
                                    <option>
                                        충전
                                    </option>
                                    <option>
                                        요금
                                    </option>
                                    <option>
                                        환불
                                    </option>
                                    <option>
                                        내정보
                                    </option>
                                    <option>
                                        가입유형
                                    </option>
                                    <option>
                                        기타
                                    </option>
                                </select>
                            </td>
                        </tr>
                        <tr>
                            <th>이름</th>
                            <td><input type="text"></td>
                        </tr>
                        <tr class="title">
                            <th>제목</th>
                            <td><input type="text"></td>
                        </tr>
                        <tr class="content">
                            <th>내용</th>
                            <td><textarea cols="80" rows="15"></textarea></td>
                        </tr>
                        <tr>
                            <th>첨부파일</th>
                            <td>
                                <div>
                                    <button>첨부하기</button>
                                </div>
                                <textarea cols="40" rows="5"></textarea>
                                <div>
                                    <button>선택삭제</button>
                                    <button>전체삭제</button>
                                </div>
                            </td>
                        </tr>
                    </table>
                    <div class="buttonBox">
                        <button>문의하기</button>
                        <button>다시쓰기</button>
                        <button type="button" onclick="location.href='/#/Inquiries'">목록으로</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <Footer/>
  </div>
</template>

<script>
import Header from '../Header'
import HeaderTop from '../HeaderTop'
import Footer from '../Footer'
import SidemenuLogin from '../SidemenuLogin'
import SidemenuListCustomer from '../SidemenuListCustomer'
import SidemenuHelp from '../SidemenuHelp'

export default {
  name: 'InquiriesWriting',
  components: {
    Header,
    Footer,
    HeaderTop,
    SidemenuLogin,
    SidemenuListCustomer,
    SidemenuHelp
  },
  data () {
    return {}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
    outline: none;
    list-style: none;
}
.csWrap {
    width: 100%;
}
.csContainer {
    width: 820px;
}
.csHeader {
    border-bottom: 2px solid #ccc;
    box-sizing: border-box;
    padding-bottom: 7px;
    margin-top: 10px;
}
.csInfo {
    width: 100%;
    border: 1px solid #ccc;
    box-sizing: border-box;
    padding: 10px 20px;
    margin: 10px 0px 20px;
}
.writingWrap {
    border-top: 3px solid #e3e3e3;
}
.writingWrap > table{
    line-height: 2.5;
    border-collapse: collapse;
    width: 100%;
}
.writingWrap > table > tr > th {
    width: 200px;
    border-bottom: 1px solid #e3e3e3;
}
.writingWrap > table > tr > td {
    border-bottom: 1px solid #e3e3e3;
}
.title > td > input {
    width: 400px;
}
.content > td > textarea {
    margin-top: 15px;
}
.buttonBox {
    margin-top: 20px;
    display: flex;
    justify-content: center;
}
.buttonBox > button {
    width: 100px;
    height: 40px;
    margin-left: 10px;
}
</style>



