<template>
  <div>
    <HeaderTop/>
    <Header/>
    <div class="chargingWrap">
      <div class="componentSection">
        <div>
          <SidemenuLogin/>
          <SidemenuListCharging action="1"/>
          <SidemenuHelp/>
        </div>
        <div class="chargingContainer">
            <div class="chargingHeader">
                <h2>충전내역</h2>
            </div>
            <div class="chargingInfo">
                <p>*고객님의 결제방식별 결제내역입니다.</p>
                <p>증빙서류의 영수증을 클릭하면 해당 결제내역에 대한 영수증 출력이 가능합니다.</p>
            </div>
            <div class="chargingNav">
                <button :class="{'active': menuIndex === 0 }" @click="menuChange(0)">가상계좌이체</button>
                <button :class="{'active': menuIndex === 1 }" @click="menuChange(1)">무통장입금</button>
                <button :class="{'active': menuIndex === 2 }" @click="menuChange(2)">신용카드결제</button>
                <button :class="{'active': menuIndex === 3 }" @click="menuChange(3)">휴대폰결제</button>
            </div>
            <div v-if="menuIndex === 0">
              <PatmentVirtual/>
            </div>
            <div v-else-if="menuIndex === 1">
            </div>
            <div v-else-if="menuIndex === 2">
              <PatmentVirtual/>
            </div>
            <div v-else-if="menuIndex === 3">
              <PatmentVirtual/>
            </div>
        </div>
      </div>
    </div>
    <Footer/>
  </div>
</template>

<script>
import Header from '../Header'
import HeaderTop from '../HeaderTop'
import Footer from '../Footer'
import PatmentVirtual from '../PatmentVirtual'
import SidemenuLogin from '../SidemenuLogin'
import SidemenuListCharging from '../SidemenuListCharging'
import SidemenuHelp from '../SidemenuHelp'

export default {
  name: 'PatmentDetails',
  components: {
    Header,
    Footer,
    HeaderTop,
    PatmentVirtual,
    SidemenuLogin,
    SidemenuListCharging,
    SidemenuHelp
  },
  data () {
    return {
      menuIndex: 0
    }
  },
  methods: {
    menuChange (index) {
      this.menuIndex = index
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
    outline: none;
    list-style: none;
}
.chargingWrap {
    width: 100%;
}
.componentSection {
    width: 1100px;
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
}
.chargingContainer {
    width: 820px;
}
.chargingHeader {
  border-bottom: 2px solid #ccc;
    box-sizing: border-box;
    padding-bottom: 7px;
    margin-top: 10px;
}
.chargingInfo {
  width: 100%;
  border: 1px solid #ccc;
  box-sizing: border-box;
  padding: 10px 20px;
  margin: 10px 0px 20px;
}
.chargingInfo > p {
    font-size: 15px;
    line-height: 1.6;
}
.chargingNav {
    display: flex;
    margin: 20px 0;
}
.chargingNav > button {
    width: 220px;
    height: 50px;
}
</style>



