<template>
<div>
    <div class="chargingContent">
        <div>
            <button>전체선택</button>
            <button>선택취소</button>
            <button>인쇄하기</button>
        </div>
        <table>
            <tr>
                <th><input type="radio"></th>
                <th>번호</th>
                <th>결제일자</th>
                <th>결제방식</th>
                <th>결제금액</th>
                <th>결제정보</th>
                <th>증빙서류</th>
            </tr>
            <tr v-for="(d, index) in listData" :key="index">
                <td><input type="radio"></td>
                <td>{{ d.number }}</td>
                <td>{{ d.day }}</td>
                <td>{{ d.lms }}</td>
                <td>{{ d.mms }}</td>
                <td>{{ d.lms }}</td>
                <td><a href="#">보기</a></td>
            </tr>
        </table>
    </div>
</div>
</template>

<script>
export default {
  name: 'PatmentVirtual',
  data () {
    return {
      listData: [
        {
          number: '1',
          day: '2021-06-10',
          lms: '카드',
          mms: '7,000,000'
        }
      ]
    }
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.chargingContent > table {
  width: 100%;
  text-align: center;
  line-height: 2;
  border: 1px solid #000;
  border-collapse: collapse;
  margin: 20px 0;
}
.chargingContent > table > tr > th {
  border: 1px solid #ccc;
  color:orchid;
}
.chargingContent > table > tr > td {
  border: 1px solid #ccc;
}
</style>



