<template>
  <div>
    <HeaderTop/>
    <Header/>
    <div class="csWrap">
        <div class="componentSection">
          <div>
            <SidemenuAfterLogin/>
            <!--<SidemenuLogin/>-->
            <SidemenuListMyInfo action="0"/>
            <SidemenuHelp/>
          </div>
            <div class="csContainer">
                <div class="csHeader">
                    <h2>회원정보 수정</h2>
                </div>
                <div class="csInfo">
                    <p>*개인정보 확인 및 수정이 가능합니다.</p>
                    <p>*내정보는 아이디/비밀번호 찾기 및 고객가입여부 확인시 필요한 정보이므로 정확히 입력하시기 바랍니다.</p>
                </div>
                <div class="writingWrap">
                    <h4>가입자 정보</h4>
                    <table>
                        <tr>
                            <th>아이디</th>
                            <td>draos15</td>
                        </tr>
                        <tr>
                            <th>가입자 이름</th>
                            <td>전영길</td>
                        </tr>
                        <tr>
                            <th>휴대폰 번호</th>
                            <td>010-5513-1549 <button>변경하기</button> 본인 명의 휴대폰으로만 변경 가능</td>
                        </tr>
                        <tr>
                            <th>가입 일자</th>
                            <td>2021-06-09</td>
                        </tr>
                    </table>
                    <h4>고객 정보</h4>
                    <table>
                        <tr>
                            <th>가입유형</th>
                            <td>개인고객</td>
                        </tr>
                        <tr class="number">
                            <th>연락처</th>
                            <td><input type="text"></td>
                        </tr>
                        <tr class="email">
                            <th>이메일</th>
                            <td><input type="text" value="ddeerr@naver.com"></td>
                        </tr>
                        <tr>
                            <th>SMS 수신여부</th>
                            <td><span><input type="radio">수신</span><span><input type="radio">거부</span><br>(광고 및 마케팅 SMS 수신 여부이며 공지 SMS는 수신 여부와 관계없이 전송됩니다.)</td>
                        </tr>
                        <tr>
                            <th>E-mail 수신여부</th>
                            <td><span><input type="radio">수신</span><span><input type="radio">거부</span><br>(광고 및 마케팅 E-mail 수신 여부이며 공지 E-mail는 수신 여부와 관계없이 전송됩니다.)</td>
                        </tr>
                    </table>
                    <div class="buttonBox">
                        <button>정보수정</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <Footer/>
  </div>
</template>

<script>
import Header from '../Header'
import HeaderTop from '../HeaderTop'
import Footer from '../Footer'
import SidemenuLogin from '../SidemenuLogin'
import SidemenuListMyInfo from '../SidemenuListMyInfo'
import SidemenuHelp from '../SidemenuHelp'
import SidemenuAfterLogin from '../SidemenuAfterLogin'

export default {
  name: 'MyInformation',
  components: {
    Header,
    Footer,
    HeaderTop,
    SidemenuLogin,
    SidemenuAfterLogin,
    SidemenuListMyInfo,
    SidemenuHelp
  },
  data () {
    return {}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
    outline: none;
    list-style: none;
}
.csWrap {
    width: 100%;
}
.csContainer {
    width: 820px;
}
.csHeader {
    border-bottom: 2px solid #ccc;
    box-sizing: border-box;
    padding-bottom: 7px;
    margin-top: 10px;
}
.csInfo {
    width: 100%;
    border: 1px solid #ccc;
    box-sizing: border-box;
    padding: 10px 20px;
    margin: 10px 0px 20px;
}
.writingWrap > h4 {
    width: 100%;
    border-bottom: 3px solid #ccc;
    padding-bottom: 7px;
    margin-top: 30px;
}
.writingWrap > table {
    width: 100%;
    border-collapse: collapse;
    line-height: 3;
}
.number > td > input {
    width: 200px;
    height: 25px;
}
.email > td > input {
    width: 350px;
    height: 25px;
}
.writingWrap > table > tr > th {
    width: 200px;
    border:1px solid #e3e3e3;
    background-color: #f6f5ff;
    font-size: 15px;
}
.writingWrap > table > tr > td {
    border:1px solid #e3e3e3;
    padding-left: 15px;
}
.writingWrap > table > tr > td > span:nth-child(2) {
    margin-left: 15px;
}
.buttonBox {
    margin-top: 20px;
    text-align: center;
}
.buttonBox > button {
    width: 150px;
    height: 50px;
}
</style>



