<template>
<div class="sidemenu__list">
    <div class="sidemenu__list__title">
        <h2>주소록</h2>
    </div>
    <ul>
        <li>
            <a :class="{actionColor: action === '0'}" href="/#/address">주소록 관리</a>
        </li>
        <li>
            <a :class="{actionColor: action === '1'}" href="/#/addressspam">수신차단 관리</a>
        </li>
    </ul>
</div>
</template>

<script>

export default {
  name: 'SidemenuList',
  components: {
  },
  props: ['action'],
  data () {
    return {
    }
  },
  methods: {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
    outline: none;
    list-style: none;
}
.sidemenu__list {
    display: flex;
    flex-direction: column;
    border: 1px solid gray;
    box-sizing: border-box;
    margin-top: 15px;
    width: 250px;

}
.sidemenu__list__title {
    box-sizing: border-box;
    padding: 10px;
}
ul > li {
    border-top: 1px solid gray;
}
ul > li > a {
    display: block;
    color: #000;
    padding: 5px 10px;
}
ul > li > a:hover {
    color: #fff;
    background: #242424;
    transition: .5s ease-in-out;
}
.actionColor {
    background: #242424;
    color: #fff;
}
</style>



