<template>
  <div>
    <HeaderTop/>
    <Header/>
    <div class="csWrap">
        <div class="componentSection">
          <div>
            <SidemenuAfterLogin/>
            <!--<SidemenuLogin/>-->
            <SidemenuListMyInfo action="1"/>
            <SidemenuHelp/>
          </div>
            <div class="csContainer">
                <div class="csHeader">
                    <h2>비밀번호 변경</h2>
                </div>
                <div class="csInfo">
                    <p>*비밀번호를 주기적으로 변경하는 것이 보안상 안전합니다.</p>
                    <p>*알기 쉽거나 연상되기 쉬운 비밀번호는 피해서 입력하시기 바랍니다.</p>
                    <p>*고객님의 실수로 타인에게 노출되어 피해를 입지 않도록 유의하시기 바랍니다.</p>
                    <p>*주민등록번호, 생일, 전화번호 등 개인정보와 관련되어 타인이 쉽게 알아낼 수 있는 번호는 피하시기 바랍니다.</p>
                </div>
                <div class="writingWrap">
                    <table>
                        <tr class="pass">
                            <th>현재 비밀번호</th>
                            <td><input type="password"></td>
                        </tr>
                        <tr class="pass">
                            <th>새 비밀번호</th>
                            <td><input type="password"><br>8~16자 영문,숫자,특수문자(~!@#$%^&*? 만 사용가능)혼합 사용</td>
                        </tr>
                        <tr class="pass">
                            <th>새 비밀번호 확인</th>
                            <td><input type="password"><br>상단에 입력한 비밀번호와 동일하게 입력해주세요.</td>
                        </tr>
                    </table>
                    <div class="buttonBox">
                        <button>변경하기</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <Footer/>
  </div>
</template>

<script>
import Header from '../Header'
import HeaderTop from '../HeaderTop'
import Footer from '../Footer'
import SidemenuLogin from '../SidemenuLogin'
import SidemenuListMyInfo from '../SidemenuListMyInfo'
import SidemenuHelp from '../SidemenuHelp'
import SidemenuAfterLogin from '../SidemenuAfterLogin'

export default {
  name: 'MyPassword',
  components: {
    Header,
    Footer,
    HeaderTop,
    SidemenuLogin,
    SidemenuAfterLogin,
    SidemenuListMyInfo,
    SidemenuHelp
  },
  data () {
    return {}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
    outline: none;
    list-style: none;
}
.csWrap {
    width: 100%;
}
.csContainer {
    width: 820px;
}
.csHeader {
    border-bottom: 2px solid #ccc;
    box-sizing: border-box;
    padding-bottom: 7px;
    margin-top: 10px;
}
.csInfo {
    width: 100%;
    border: 1px solid #ccc;
    box-sizing: border-box;
    padding: 10px 20px;
    margin: 10px 0px 20px;
}
.writingWrap > table {
    width: 100%;
    border-collapse: collapse;
    line-height: 3;
}
.pass > td > input {
    width: 300px;
    height: 25px;
}
.writingWrap > table > tr > th {
    width: 200px;
    border:1px solid #e3e3e3;
    background-color: #f6f5ff;
    font-size: 15px;
}
.writingWrap > table > tr > td {
    border:1px solid #e3e3e3;
    padding-left: 15px;
}
.buttonBox {
    margin-top: 20px;
    text-align: center;
}
.buttonBox > button {
    width: 150px;
    height: 50px;
}
</style>



