<template>
<div>
    <div class="sidemenu__login">
        <div class="sidemenu__login__top">
            <span>[{{ type }}]</span>
            <span>{{ name }} 님</span>
        </div>
        <div class="sidemenu__login__middle">
            <table>
                <tr>
                    <th><img src="https://www.webmunja.co.kr/img/common/b_sms.png"> 단문 :</th>
                    <td>5 건</td>
                </tr>
                <tr>
                    <th><img src="https://www.webmunja.co.kr/img/common/b_lms.png"> 장문 :</th>
                    <td>3 건</td>
                </tr>
                <tr>
                    <th><img src="https://www.webmunja.co.kr/img/common/b_mms.png"> 사진 :</th>
                    <td>1 건</td>
                </tr>
            </table>
            <button @click="($router.push('charging'))">충전하기</button>
        </div>
        <div class="sidemenu__login__bottom">
            <button @click="($router.push('myinformation'))">내정보</button>
            <button>로그아웃</button>
        </div>
    </div>
</div>
</template>
<script>

export default {
  name: 'SidemenuAfterLogin',
  data () {
    return {
      menuIndex: 0,
      type: '개인고객',
      name: '홍 민'
    }
  },
  methods: {
    find () {
      window.open('/#/FindId', 'a', 'width=500, height=600, left=700, top=200')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
    outline: none;
    list-style: none;
}
.wrap {
    width: 100%;
    height: 100%;
}
.sidemenu {
    width: 250px;
    height: 100%;
}
.sidemenu__login {
    border: 1px solid #000;
    box-sizing: border-box;
    padding: 15px;
    margin-top: 10px;
}
.sidemenu__login__top {
    display: flex;
    flex-direction: column;
    text-align: center;
    padding-bottom: 5px;
    border-bottom: 1px solid #ccc;
}
.sidemenu__login__middle {
    line-height: 2.5;
}
.sidemenu__login__middle > table {
    width: 100%;
    margin-top: 10px;
}
.sidemenu__login__middle > table > tr > th {
    width: 100px;
}
.sidemenu__login__middle > table > tr > td {
    text-align: right;
    padding-right: 10px;
}
.sidemenu__login__middle > button {
    width: 100%;
    max-width: 220px;
    margin-top: 5px;
    height: 40px;
}
.sidemenu__login__middle img {
    width: 22px;
    height: 22px;
    margin-right: 5px;
}
.sidemenu__login__bottom {
    margin-top: 10px;
}
.sidemenu__login__bottom button {
    width: 100px;
    height: 35px;
}
.sidemenu__login__bottom button:nth-child(2) {
    margin-left: 15px;
}
</style>



