<template>
  <div>
    <HeaderTop/>
    <Header/>
    <div class="csWrap">
        <div class="componentSection">
          <div>
            <SidemenuLogin/>
            <SidemenuSpam action="2"/>
            <SidemenuHelp/>
          </div>
            <div class="csContainer">
                <div class="csHeader">
                    <h2>광고 및 스팸문자정책</h2>
                </div>
                <div class="csInfo">
                    <h4>[스팸관리정책]</h4>
                    <p>정보통신망이용촉진 및 정보보호등에관한 법률(정보통신망법) 제50조에 의거하여 모든 휴대폰 스팸 메시지에 대해서 발송을 원천적으로 금하고 있습니다.</p>
                    &nbsp;
                    <h4>1. 용어정리</h4>
                    <p>ο 스팸 : 수신자의 의사에 반하여 전달되는 영리를 목적으로 하는 모든 유형의 상업성 정보 (정보통신망법 제50조 제1항)</p>
                    <p>ο 스팸SMS : 사전에 수신동의를 하지 않은 고객에게 전송하는 SMS 및 야간시간(21시~8시)에 전송되는 모든 광고성 SMS</p>
                    <p>ο Opt-In : 사전에 수신을 동의한 고객에게만 광고SMS를 발송할 수 있는 방식</p>
                    &nbsp;
                    <h4>2. 스팸SMS 규제방안</h4>
                    <p>- 모든 상업성 SMS는 Opt-In 방식, 즉 사전동의를 받은 고객에 대해서만 전송이 가능합니다.</p>
                    <p>- 야간 시간대 휴대폰광고는 전송이 금지됩니다.(21시~8시), 야간 시간대 발송하실 경우 추가동의를 받으셔야 합니다.</p>
                    <p>- 청소년에게 전송되는 모든 음란성 휴대폰 메시지는 일체 금지됩니다.</p>
                    <p>- 위반시 고객님의 사전동의 없이 서비스 사용이 중단될수도 있습니다. 서비스 사용중지시, 회원의 사이버머니(캐쉬), 마일리지포인트는 현금으로 환불되지 않으며, 소멸됩니다.</p>
                    <p>- 휴대폰 문자광고는 "[광고]" 문구와 무료 수신거부번호 080번호를 꼭 기입하셔야 합니다.</p>
                    &nbsp;
                    <h4>3. 700, 060, 030 발송제한</h4>
                    <p>700, 060, 030 번호를 회신번호 또는 메시지 내용에 사용하는 광고성 SMS의경우에는 반드시 사전에 수신동의 여부를 확인할 수 있는 자료를 관리자에게 제출하여야 합니다.</p>
                    <p>모든 회원께서는 스팸관리규정을 준수하여 주시기 바랍니다. 이용에 불편이 없도록 최선을 다하겠습니다.</p>
                </div>
            </div>
        </div>
    </div>
    <Footer/>
  </div>
</template>

<script>
import Header from '../Header'
import HeaderTop from '../HeaderTop'
import Footer from '../Footer'
import SidemenuLogin from '../SidemenuLogin'
import SidemenuSpam from '../SidemenuSpam'
import SidemenuHelp from '../SidemenuHelp'

export default {
  name: 'Spam',
  components: {
    Header,
    Footer,
    HeaderTop,
    SidemenuLogin,
    SidemenuSpam,
    SidemenuHelp
  },
  data () {
    return {}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
    outline: none;
    list-style: none;
}
.csWrap {
    width: 100%;
}
.csContainer {
    width: 820px;
}
.csHeader {
    border-bottom: 2px solid #ccc;
    padding-bottom: 7px;
    margin-top: 10px;
}
.csInfo {
    margin: 40px 0;
    line-height: 1.6;
    padding: 20px;
    font-size: 14px;
}
.csInfo > h3 {
    margin-top: 20px;
    border-bottom: 1px solid #ccc;
    padding-bottom: 5px;
}
.csInfo > h4 {
    margin-top: 20px;
    padding-bottom: 10px;
}
</style>



