<template>
  <div>
    <HeaderTop/>
    <Header/>
    <div class="csWrap">
        <div class="componentSection">
          <div>
            <SidemenuLogin/>
            <SidemenuShip action="1"/>
            <SidemenuHelp/>
          </div>
            <div class="csContainer">
                <div class="csHeader">
                    <h2>이용안내</h2>
                </div>
                <div class="csInfo">
                    <p>문자마트의 모든서비스는 회원가입 후 정상적으로 이용이 가능하며 만 18세 미만은 가입하실 수 없습니다.</p>
                    <p>문자메시지,장문메시지 서비스를 이용하기 위해서는 로그인 후 사이버머니를 충전하셔야 합니다.</p>
                    <p>충전하신 사이버머니는 소멸기한이 없습니다.</p>
                    <p>이용하실 수 있는 기능은 아래와 같습니다.</p>
                    &nbsp;
                    <p>01. 그룹별로 관리할 수 있는 주소록</p>
                    &nbsp;
                    <p>02. 여러개의 회신번호를 관리할 수 있는 보관함</p>
                    &nbsp;
                    <p>03. 자주 사용하는 메시지를 보관할 수 있는 메시지보관함</p>
                    &nbsp;
                    <p>04. 상세한 전송결과내역 및 엑셀파일로 다운로드 제공</p>
                    &nbsp;
                    <p>05. 문자 전송 시 중복되는 수신자 정보를 걸러내는 필터링 기능</p>
                    &nbsp;
                    <p>06. 단문메시지(SMS) , 장문메시지(LMS) 모두 한번에 3,000명까지 전송 가능</p>
                    &nbsp;
                    <h4>문자메시지 보내기</h4>
                    <h4>받는사람 추가하는 방법</h4>
                    &nbsp;
                    <p>01. 주소록 불러오기</p>
                    &nbsp;
                    <p>- 개별보기에서 선택 후 주소추가 버튼을 클릭하면 받는사람 리스트로 이동됩니다.</p>
                    <p>- 그룹보기에서 그룹을 선택 후 주소추가 버튼을 클릭하면 그룹에 속한 주소가 받는사람 리스트로 이동됩니다.</p>
                    &nbsp;
                    <p>02. 외부데이터(예: 엑셀) 불러오기</p>
                    &nbsp;
                    <p>- 휴대폰번호만 있는 경우 : 휴대폰번호가 입력된 열(컬럼, Column)만 복사하신 후 붙여넣기 버튼을 클릭하시면 주소록 상자에 복사한 핸드폰번호가 나타납니다. 자료 확인 후에 받는사람 리스트로 보내기 버튼을 클릭하시면 받는사람 리스트로 이동됩니다.</p>
                    &nbsp;
                    <p>- 이름과 휴대폰번호가 있는 경우 : 이름과 휴대폰번호가 각각의 열에 있어야 합니다. 두 개의 열을 복사하신 후 붙여넣기 버튼을 클릭하시고 나머지는 상기 사항을 참고하세요.</p>
                    &nbsp;
                    <p>03. 빠른 추가 직접입력</p>
                    &nbsp;
                    <p>- 이름과 휴대폰번호 혹은 휴대폰번호만 입력하신 후 추가 버튼을 클릭하시면 받는사람 리스트에 추가됩니다.</p>
                    &nbsp;
                    <p>04. 받는 사람 직접입력</p>
                    &nbsp;
                    <p>- 한 명에게 보낼 때 사용합니다. 휴대폰번호를 받는사람 란에 입력해 주세요.</p>
                    &nbsp;
                    <p>05. 최근보낸번호</p>
                    &nbsp;
                    <p>- 최근에 전송한 번호를 5개까지 보여줍니다. 선택하시면 받는 사람란에 입력됩니다.</p>
                    &nbsp;
                    <h4>문자메시지 내용 작성 방법</h4>
                    &nbsp;
                    <p>01. 단문: 최대 90 byte(한글 40자)까지 내용을 입력하실 수 있습니다.</p>
                    &nbsp;
                    <p>02. 단문연속: 단문연속을 클릭하시면 입력란이 3개가 나타납니다.<br>각각 내용을 입력하시면 차례대로 5초 간격으로 전송하며 240 byte까지 입력하실 수 있습니다.</p>
                    &nbsp;
                    <p>문자메시지 내용 작성 방법</p>
                </div>
            </div>
        </div>
    </div>
    <Footer/>
  </div>
</template>

<script>
import Header from '../Header'
import HeaderTop from '../HeaderTop'
import Footer from '../Footer'
import SidemenuLogin from '../SidemenuLogin'
import SidemenuShip from '../SidemenuShip'
import SidemenuHelp from '../SidemenuHelp'

export default {
  name: 'UserInfo',
  components: {
    Header,
    Footer,
    HeaderTop,
    SidemenuLogin,
    SidemenuShip,
    SidemenuHelp
  },
  data () {
    return {}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
    outline: none;
    list-style: none;
}
.csWrap {
    width: 100%;
}
.csContainer {
    width: 820px;
}
.csHeader {
    border-bottom: 2px solid #ccc;
    padding-bottom: 7px;
    margin-top: 10px;
}
.csInfo {
    margin: 40px 0;
    line-height: 1.6;
    padding: 20px;
    font-size: 14px;
}
.csInfo > h3 {
    margin-top: 20px;
    border-bottom: 1px solid #ccc;
    padding-bottom: 5px;
}
.csInfo > h4 {
    margin-top: 20px;
    padding-bottom: 10px;
}
</style>



