<template>
<div class="wrap">
    <div class="joinSection">
      <div class="joinSection__title">
        <h2>회원가입</h2>
      </div>
      <div class="joinSection__terms">
        <span>이용약관동의</span>
        <div class="joinSection__terms__box">
          <h3>제 1장 총칙</h3>
          <h4>제 1조(목적)</h4>
          <p>이 약관은 (주) 아이테크 (이하 "회사")가 제공하는 인터넷 서비스를 이용함에 있어 이용자의 권리/의무 및 책임사항, 기타 필요사항을 규정함을 목적으로 합니다.</p>
          <h4>제 2조(정의)</h4>
          <p>1. 웹문자 : 회사의 서비스명으로서 이용자가 전송하고자 하는 내용ㆍ정보 등을 문자메시지, 멀티미디어메시지, 팩스 등 다양한 메시지 형태로 변화시켜 전송하는 서비스 및 이용자가 제3자로부터 메시지를 수신할 수 있도록 하는 서비스<br>
          2. 이용자 : 회사와 서비스 이용계약을 체결한자<br>
          3. 이용계약 : 서비스를 제공받기 위하여 회사와 이용자간에 체결하는 계약<br>
          4. 이용신청 : 회사가 정한 별도의 기준과 절차에 따라 서비스 이용을 신청하는 것<br>
          5. 이용정지 : 회사가 정한 일정한 요건에 따라 일정기간 동안 서비스 이용을 보류하는 것<br>
          6. 해지 : 이용자가 서비스 개통 후에 이용계약을 해약하는 것<br>
          7. 이용자 : 회사와 서비스 이용계약을 체결한 자 또는 회사<br>
          8. 아이디 (ID) : 이용자 식별과 서비스 이용을 위하여 이용자가 선정하고 회사가 인정한 문자와 숫자의 조합<br>
          9. 비밀번호 : 이용 아이디와 일치된 이용자임을 확인하고, 이용자의 비밀을 보호하기 위하여 이용자가 설정한 문자와 숫자의 조합<br>
          10. 휴면아이디 : 12개월 이상 계속해서 로그인을 포함한 서비스 이용이 없는 아이디<br>
          11. 스팸메시지 : 수신자의 의사에 반하여 정보통신망을 통해 일방적으로 전송 또는 게시되는 영리목적의 광고성 정보<br>
          12. 문자피싱메시지 : 전자금융사기를 목적으로 전송 또는 게시되는 정보<br>
          13. 스미싱메시지 : 메시지 내용 중 인터넷 주소를 클릭하면 악성코드가 설치되어 수신자가 모르는 사이에 금전적 피해 또는 개인ㆍ금융정보 탈취 피해를 야기하는 메시지<br>
          14. 후불제 : 이용자가 회사에서 제공하는 서비스를 선 이용 후 회사에서 지정한 날짜에 서비스 이용에 대한 지불을 하는 것</p>
          <h4>제 3조(약관의 게시와 개정)</h4>
          <p>1. 회사는 이 약관의 내용을 이용자가 쉽게 알 수 있도록 서비스 초기 화면에 게시합니다.<br>
          2. 회사는 "약관규제에관한법률", "정보통신망이용촉진및정보보호등에관한법률(이하 "정보통신망법")", "전자상거래 등에서의 소비자보호에관한법률" 등 관련법을 위배하지 않는 범위에서 이 약관을 개정할 수 있습니다.<br>
          3. 회사가 약관을 개정할 경우에는 적용일자 및 개정사유를 명시하여 현행약관과 함께 제1항의 방식에 따라 그 개정약관의 적용일자 7일 전부터 적용일자 전까지 공지합니다.
          다만, 이용자의 권리 또는 의무에 관한 중요한 규정의 변경은 최소한 30일 전에 공지하고 일정기간 서비스내 공지사항, 전자우편, 로그인시 동의창 등의 전자적 수단을 통해 따로 명확히 통지하도록 합니다.<br>
          4. 회사가 전항에 따라 개정약관을 공지 또는 통지하면서 이용자에게 약관 변경 적용일까지 거부의사를 표시하지 않으면 동의한 것으로 본다는 뜻을 명확하게 공지 또는 통지하였음에도 이용자가 명시적으로 거부의 의사표시를 하지 아니한 경우 이용자가 개정약관에 동의한 것으로 봅니다.<br>
          5. 이용자가 개정약관의 적용에 동의하지 않을 경우 회사는 개정약관의 내용을 적용할 수 없으며, 이 경우 이용자는 이용계약을 해지할 수 있습니다. 다만, 기존 약관을 적용할 수 없는 특별한 사정이 있는 경우에는 회사는 이용계약을 해지할 수 있습니다.<br>
          6. 이용자는 약관의 변경에 대하여 주의의무를 다하여야 하며 변경된 약관으로 인한 이용자의 피해는 회사가 책임지지 않습니다.<br>
          7. 이 약관의 적용기간은 이용자의 가입일 부터 해지일 까지로 규정합니다. 단, 채권 또는 채무관계가 있을 경우에는 채권, 채무의 완료일 까지로 규정합니다.<br></p>
          <h3>제 2장 이용자의 이용계약 및 관리</h3>
          <h4>제 4조(이용계약 체결)</h4>
          <p>이 약관은 (주) 아이테크(이하 "회사")가 제공하는 인터넷 서비스를 이용함에 있어 이용자의 권리/의무 및 책임사항, 기타 필요사항을 규정함을 목적으로 합니다.</p>
        </div>
        <div class="joinSection__terms__select">
          <label for="agree">
            <input type="radio" name="select" id="agree">동의합니다. <span>(필수)</span>
          </label>
          <label for="disagree">
            <input type="radio" name="select" id="disagree">동의하지 않습니다.
          </label>
        </div>
      </div>
      <div class="joinSection__terms">
        <span>개인정보 수집 및 이용에 관한 동의</span>
        <div class="joinSection__terms__box1">
          <h4>수집하는 개인정보 항목</h4>
          <p>회사는 개인정보취급방침을 통하여 고객님께서 제공하시는 개인정보가 어떠한 용도와 방식으로 이용되고 있으며, 개인정보보호를 위해 어떠한 조치가 취해지고 있는지 알려드립니다.</p>
          &nbsp;
          <h4>회사는 수집한 개인정보를 다음의 목적을 위해 활용합니다..</h4>
          <p>ο 서비스 제공에 관한 계약 이행 및 서비스 제공에 따른 요금정산</p>
          <p>ο 콘텐츠 제공 , 구매 및 요금 결제 , 물품배송 또는 청구지 등 발송</p>
          <p>ο 회원 관리</p>
          &nbsp;
          <h4> 개인정보의 파기절차 및 방법</h4>
          <p>- 회사는 이용자의 개인정보를 원칙적으로 외부에 제공하지 않습니다. 다만, 아래의 경우에는 예외로 합니다.</p>
          <p>- 야간 시간대 휴대폰광고는 전송이 금지됩니다.(21시~8시), 야간 시간대 발송하실 경우 추가동의를 받으셔야 합니다.</p>
          <p>- 회사는 고객님의 동의없이 고객님의 정보를 외부 업체에 위탁하지 않습니다. 향후 그러한 필요가 생길 경우, 위탁 대상자와 위탁 업무 내용에 대해 고객님에게 통지하고 필요한 경우 사전 동의를 받도록 하겠습니다.</p>
          <p>-  전자적 파일형태로 저장된 개인정보는 기록을 재생할 수 없는 기술적 방법을 사용하여 삭제합니다.</p>
          <p>- 휴대폰 문자광고는 "[광고]" 문구와 무료 수신거부번호 080번호를 꼭 기입하셔야 합니다.</p>
          &nbsp;
          <h4>개인정보의 보유 및 이용기간</h4>
          <p>회원님이 회원가입 등을 위해 입력하신 정보는 목적이 달성된 후 별도의 DB로 옮겨져(종이의 경우 별도의 서류함) 내부 방침 및 기타 관련 법령에 의한 정보보호 사유에 따라(보유 및 이용기간 참조) 일정 기간 저장된 후 파기되어집니다.</p>
          <p>별도 DB로 옮겨진 개인정보는 법률에 의한 경우가 아니고서는 보유되어지는 이외의 다른 목적으로 이용되지 않습니다.</p>
        </div>
        <div class="joinSection__terms__select">
          <label for="agree1">
            <input type="radio" name="select1" id="agree1">동의합니다. <span>(필수)</span>
          </label>
          <label for="disagree1">
            <input type="radio" name="select1" id="disagree1">동의하지 않습니다.
          </label>
        </div>
      </div>
      <div class="joinSection__button">
        <button type="button" onclick="location.href='/#/join'">다음</button>
        <button>취소</button>
      </div>
    </div>
</div>
</template>

<script>

export default {
  name: 'JoinTerms',
  components: {
  },
  data () {
    return {
    }
  },
  methods: {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
    outline: none;
    list-style: none;
}
.wrap {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100%;
}
.joinSection {
  display: flex;
  flex-direction: column;
  margin-top: 10px;
}
.joinSection__title {
  width: 850px;
  padding-bottom: 7px;
  border-bottom: 1px solid gray;
}
.joinSection__terms {
  width: 850px;
  margin-top: 15px;
}
.joinSection__terms__box {
  width: 100%;
  height: 400px;
  border: 1px solid #c1c1c1;
  overflow-y: scroll;
  line-height: 1.6;
  box-sizing: border-box;
  padding: 20px;
  font-size: 14px;
  margin-top: 15px
}
.joinSection__terms__box > h3 {
  border-bottom: 1px solid #ccc;
  padding-bottom: 5px;
}
.joinSection__terms__box > h4 {
  margin-top: 20px;
  padding-bottom: 10px;
}
.joinSection__terms__select {
  margin-top: 10px;
  text-align: center;
}
.joinSection__terms__select span {
  color: red;
  font-weight: 500;
}
.joinSection__terms__select label:nth-child(2) {
  margin-left: 80px;
}
.joinSection__terms__box1 {
  width: 100%;
  height: 200px;
  border: 1px solid #c1c1c1;
  overflow-y: scroll;
  line-height: 1.6;
  box-sizing: border-box;
  padding: 20px;
  font-size: 14px;
  margin-top: 15px
}
.joinSection__button {
  width: 100%;
  text-align: center;
  margin-top: 20px;
}
.joinSection__button button {
  width: 80px;
  height: 50px;
  border-radius: 10px;
  background: #ededed;
  color: #000;
  border: 0px;
  font-size: 23px;
  cursor: pointer;
}
.joinSection__button button:nth-child(1) {
  background: #009e4f;
  color: #fff;
}
</style>



